

import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import { Plan, PlanAttributes, DeconstructedHashPlan } from "@/models";
@Component({
  components: {}
})
export default class VPlanList extends Vue {
  // ---------- Props ----------
  @Prop() plans!: Array<DeconstructedHashPlan>;

  @Prop() title!: string;

  @Prop() showPlanCount!: boolean;
  // ------- Local Vars --------

  // --------- Watchers --------

  @Watch("totalPlansLeftToAssign")
  changeInPlans() {
    if (this.totalPlansLeftToAssign == 0) {
      this.$emit("can-advance", true);
    } else {
      this.$emit("can-advance", false);
    }
  }

  // ------- Lifecycle ---------
  constructor() {
    super();
  }
  // --------- Methods ---------

  camerasRemaining(plan: DeconstructedHashPlan) {
    return plan.planData.numCameras - plan.planData.camerasAssigned;
  }
  getColor(plan: DeconstructedHashPlan) {
    const cameraCount = this.showPlanCount
      ? plan.planData.numCameras
      : this.camerasRemaining(plan);
    if (cameraCount > 0) {
      return "primary";
    } else if (cameraCount == 0) {
      return "secondary";
    } else {
      return "error";
    }
  }

  get totalPlansLeftToAssign() {
    if (this.plans.length > 0) {
      return this.plans
        .map(plan => {
          return this.showPlanCount
            ? plan.planData.numCameras
            : this.camerasRemaining(plan);
        })
        .reduce((prevPlan, currPlan) => {
          return prevPlan + currPlan;
        });
    } else {
      return 999;
    }
  }
}
